<template>
	<!-- 首页 -->
	<view class="lml-index-box">
		<!-- 微信小程序 搜索框 -->
		<!-- #ifdef MP-WEIXIN -->
		<searchInput></searchInput>
		<!-- #endif -->
	
		 <!-- 轮播图 -->
		 <Swiper :bannerList="lmlBannerList"></Swiper>
		
				<!-- 下拉刷新 上拉加载 -->
			<!-- <mescroll-body 
				ref="mescrollRef"
				@init="mescrollInit" 
				@down="downCallback" 
				@up="upCallback" 
				:down="downOption"
				:up="upOption"
				> -->
				
			
				<!-- 分类区功能 -->
				<lmlNav  :categoryList="lmlCategory"></lmlNav>
					<view class="lml-list">
				<swiperCourse name="热门推荐" word="HOT" :courseData="lmlCourseList"></swiperCourse>
				
				</view>
				<!-- </mescroll-body> -->
				
	</view>
</template>

<script>
	import searchInput from '@/components/search-input/search-input.vue'//微信小程序 搜索框
	import Swiper from '@/components/swiper/swiper.vue'//轮播图
	
	import indexApi from '@/api/index.js'//封装api
	
	import lmlNav from '@/pages/lml-index/components/lml-Nav.vue'//分类区功能
	import swiperCourse from '@/pages/lml-index/components/swiper-course.vue'//热门推荐
	
	
	
	// 引入mescroll-mixins.js  
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js"
	
	export default {
		// 使用mixin
		mixins: [MescrollMixin],
		components:{
			searchInput,//微信小程序 搜索框
			Swiper,//轮播图
			lmlNav,//分类区功能
			swiperCourse,//热门推荐
		},
		data() {
			return {
				lmlBannerList:[],// 轮播图 数据
				lmlCategory:[],// 分类区 数据
				lmlCourseList:[],//热门推荐
				downOption:{},
				upOption:{},
			};
		},
		created() {
			this.lmlgetBanner()
			this.lmlgetCategory()
			this.lmlgetCourseList()
		},
		
		methods:{
			// 请求轮播图 数据
		async lmlgetBanner(){
			this.lmlBannerList=await indexApi.getBanner()
			
			},
			
			// 请求 分类区数据
			async lmlgetCategory(){
				this.lmlCategory =await indexApi.getCategory()
			},
			
			// 请求 热们推荐数据 
			async lmlgetCourseList(){
				this.lmlCourseList =await indexApi.getCategory()
				console.log(this.lmlCourseList)
			},
			
			downCallback(){},
			upCallback(){},
		}
	}
</script>

<style lang="scss">
	// 首页 大盒子 
.lml-index-box{
	.lml-list{
		padding: 0 30rpx ;
	}
}
</style>
